import React from "react";
import './style.css'
import Button from '@mui/material/Button';
import Grid from '@mui/material/Grid';


const Nav = ({changeRoute})=>{
    return (
		<Grid container justifyContent="space-between">
			<Grid item sx={{marginBottom:'auto', marginTop:'auto', fontSize:'1.2em'}}>
				Expense Tracker
			</Grid>
			<Grid item>
				<Button size = 'medium' color='error' onClick={()=>changeRoute('signin')}>Log Out</Button>
			</Grid>
		</Grid>
    )
}

export default Nav;